*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width:100%;
    /* font-family: ;     */
}

body{
    background-color: white;
}
:root{
    --typeColor: black;
}

.Header{
    width: 100%;
    background-color: rgb(192, 192, 192);
    padding: 15px;
    text-align: center;
}
.CardSection{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    background-color: #eee;
    padding : 20px;
    width: 100%;
    height: 100%;
}

.card{
    width: 200px;
    min-width: 100px;
    height: 170px;
    border: 2px solid;
    border-radius: 15px;
    box-shadow: 5px 10px 8px #888888;
    margin: 20px;
}

.card:hover{
    cursor: pointer;
}

.cardNumber{
    position: relative;
    top: 0px;
    left: 0px;
    width: 40px;
    text-align: right;
    border-radius: 10px 0 15px 0;
    overflow: hidden;
}

.cardNumber p{
    padding: 3px;
    padding-right: 15px;
}

.cardContent{
    width: 100%;
    text-align: center;   
}

.pokeImg{
    width: 90px;
    height: 100%;
}


.RoletaSection{
    background-color: #ccc;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
}

.RoletaButton{
    width: 130px;
    height: 50px;
    background-color: red;
    font-size: 20px;
    font-weight: bold;
    border: 1px solid black;
    border-radius: 10px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.RoletaButton:hover{
    cursor: pointer;
    background-color: blue;
}

.Roletas{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
}

.roletaCards{
    width: 160px;
    height: 160px;
    border: 2px solid black;
    border-radius: 10px;
    margin: 10px 20px;
    /* transform-style: preserve-3d; */
}

.pokeImgRoullet{
    width: 160px;
    height: 100%;
}



